.navbar {
  width: 100%;
  height: 130px;
  background-color: #222222;
  display: flex;
  align-items: center;
  position: sticky;
  z-index: 10;
  left: 0;
  top: 0;
  padding: 0 100px;
  transition: all 0.7s ease;
}
.navbar.homeNav {
  background-color: rgb(23, 23, 23);
}
.navbar.scrolled {
  background-color: rgb(23, 23, 23);
}
.navbar .leftNav {
  width: 50%;
  display: flex;
  align-items: center;
}
.navbar .leftNav img {
  height: 100px;
}
.navbar .rightNav {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  margin-bottom: 40px;
}
.navbar .rightNav .btns .navBtn {
  padding: 15px 45px;
  border: none;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 1px;
  cursor: pointer;
  text-decoration: none;
}
.navbar .rightNav .btns .navBtn.joinNow {
  -webkit-clip-path: polygon(15% 0%, 100% 0%, 87% 100%, 0% 100%);
          clip-path: polygon(15% 0%, 100% 0%, 87% 100%, 0% 100%);
  background: #ffffff;
  color: #038d9f;
}
.navbar .rightNav .btns .navBtn.freeClass {
  padding: 15px 35px;
  -webkit-clip-path: polygon(14% 0%, 100% 0%, 100% 100%, 0% 100%);
          clip-path: polygon(14% 0%, 100% 0%, 100% 100%, 0% 100%);
  margin-left: -23.5px;
  background: #038d9f;
  color: #ffffff;
}
.navbar .rightNav .btns.servicePage .joinNow {
  display: none;
}
.navbar .rightNav .btns.servicePage .freeClass {
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%) !important;
          clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%) !important;
}
.navbar .rightNav .menus {
  margin-top: 20px;
}
.navbar .rightNav .menus ul {
  display: flex;
  list-style: none;
  gap: 30px;
}
.navbar .rightNav .menus ul li {
  position: relative;
}
.navbar .rightNav .menus ul li a {
  text-decoration: none;
  color: #ffffff;
  font-weight: 400;
  font-size: 18px;
}
.navbar .rightNav .menus ul li.active::after {
  content: "";
  width: 48px;
  height: 5px;
  bottom: -12px;
  left: 0;
  transform: skew(-40deg);
  position: absolute;
  background-color: #038d9f;
}

.mobileNavbar {
